import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index/index'// 首页
import Pay from '@/pages/pay/pay'//缴费页面
import PayStop from '@/pages/pay/pay-stop'//停车缴费
import Paymentquery from '@/pages/paymentquery/paymentquery'//缴费查询
import Suggest from '@/pages/suggest/suggest'//用户评论
import Myhouse from '@/pages/house/myhouse'//绑定房子
import Addhouse from '@/pages/house/add-house'
import Bindhouse from '@/pages/house/bind-house'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path:'/pay',
      name:'pay',
      component:Pay,
      meta:{
        title:'缴费页面'
      }
    },
    {
      path:'/pay-stop',
      name:'pay-stop',
      component:PayStop,
      meta:{
        title:'停车缴费'
      }
    },
    {
      path:'/paymentquery',
      name:'paymentquery',
      component:Paymentquery
    },
    {
      path:'/suggest',
      name:'suggest',
      component:Suggest
    },
    {
      path:'/myhouse',
      name:'myhouse',
      component:Myhouse,
      meta:{
        title:'我的房子'
      }
    },
    {
      path:'/addhouse',
      name:'addhouse',
      component:Addhouse,
      meta:{
        title:'添加房子'
      }
    },
    {
      path:'/bindhouse/:number',
      name:'bindhouse',
      component:Bindhouse,
      meta:{
        title:'绑定房子'
      }
    }
  ]
})



